<template>
  <section class="section">
    <h1>This addresses cannot be processed</h1>
    <b-table :data="badContibutorsToRender" :columns="columns"></b-table>
    <br />
    <p>Do you want proceed with rest of addresses?</p>
    <br />
    <b-button
      id="confirm-proceed-button"
      class="button is-info"
      :loading="isLoading"
      @click="nextPage"
    >
      Yes
    </b-button>
    <b-button id="confirm-reject-button" class="button is-info" @click="abordMission">No</b-button>
    <br />
    <br />
    <b-message type="is-success" :active.sync="isLoading">
      {{ currentCheck.method }} from {{ currentCheck.from
      }}<span v-if="currentCheck.to"> to </span> {{ currentCheck.to }}
    </b-message>
  </section>
</template>
<script>
import { mapState, mapGetters } from 'vuex'

export default {
  data() {
    return {
      columns: [
        {
          field: 'address',
          label: 'Address',
          width: '100'
        },
        {
          field: 'balance',
          label: 'Amount',
          width: '100'
        }
      ],
      isLoading: false
    }
  },
  computed: {
    ...mapState('distribution', ['badContributors', 'currentCheck']),
    ...mapGetters('token', ['toDecimals']),
    badContibutorsToRender() {
      return this.badContributors.map(contributor => {
        return {
          address: contributor.address,
          balance: this.toDecimals(contributor.balance)
        }
      })
    }
  },
  mounted() {
    this.$nuxt.$loading.finish()
  },
  beforeCreate() {
    if (this.$store.state.distribution.badContributors.length === 0) {
      this.$store.dispatch('router/goToIndex')
    }
  },
  methods: {
    async nextPage() {
      this.isLoading = true
      this.$store.dispatch('distribution/removeBadAddresses')
      await this.$store.dispatch('distribution/prepareTxs')

      const nextPage = 'summary'
      this.$store.dispatch('router/setNextPageAndGo', { nextPage })
      this.isLoading = false
    },
    abordMission() {
      this.$store.dispatch('router/goToIndex')
    }
  }
}
</script>
